import { Button } from "react-bootstrap";
import { useTranslation } from "react-i18next";

import "./CallUsForBooking.css";

function CallUsForBooking() {
  const { t } = useTranslation();
  return (
    <div className="call-req-div">
      <div className="container">
        <div className="row d-flex justify-content-center hero-text pt-5 pb-5">
          <div className="col-sm-7 center mt-5 mb-5">
            <hr className="hr-tag-blue" />
            <h2 className="head-text-style mt-4">{t("callUs.head")}</h2>
            <p className="gray-text-style">{t("callUs.sub")}</p>
            <Button href="tel:1(844) 344-1722" variant="info">
              {t("callUs.btn")}
            </Button>
          </div>
        </div>
      </div>
    </div>
  );
}

export default CallUsForBooking;
