import { useTranslation } from "react-i18next";
import LazyLoad from "react-lazyload";

import CallUsForBooking from "../../../components/CallUsForBooking/CallUsForBooking";
import NavBar from "../../../components/NavBar/NavBar";

import "./About.css";

function About() {
  const { t } = useTranslation();
  return (
    <div className="about">
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-2">
          <div className="col-sm-9 center">
            <h2 className="header-text-1">{t("about.txt_1")}</h2>
            <p className="header-text-3">{t("about.txt_2")}</p>
          </div>
        </div>
      </div>
      <LazyLoad height={200} once>
        <CallUsForBooking />
      </LazyLoad>
    </div>
  );
}

export default About;
